/********other mixins **********/
.html-contact-page .contact-page .page-title {
  margin: 25px;
}
.html-contact-page .contact-page .page-title h1 {
  font-size: 1.875rem;
}
.html-contact-page .contact-page .page-body {
  max-width: 740px;
  margin: 0 auto;
}
.html-contact-page .contact-page .page-body form {
  padding: 50px;
  background-color: #F6F6F6;
}
.html-contact-page .contact-page .page-body form .fieldset {
  background-color: #ffffff;
  padding: 40px;
}
.html-contact-page .contact-page .page-body form .fieldset .inputs textarea {
  width: 560px;
  max-width: 100%;
}